import { CompletionContext } from "@codemirror/autocomplete";

const langMap = {
  javascript: async () => await import("@codemirror/lang-javascript"),
  css: async () => await import("@codemirror/lang-css"),
  html: async () => await import("@codemirror/lang-html"),
};

function myCompletions(context: CompletionContext) {
  let word = context.matchBefore(/\w*/);
  if (word.from == word.to && !context.explicit) return null;
  return {
    from: word.from,
    options: [
      { label: "match", type: "keyword" },
      { label: "hello", type: "variable", info: "(World)" },
      { label: "magic", type: "text", apply: "⠁⭒*.✩.*⭒⠁", detail: "macro" },
    ],
  };
}

export const getEditorLangModule = async (lang: string) => {
  const result = await langMap[lang]?.();
  if (result) {
    const codeLanguage = result?.[`${lang}Language`];
    const codeLanguageCompletions = codeLanguage.data.of({
      autocomplete: myCompletions,
    });
    return [result?.[lang], codeLanguageCompletions];
  }
  return undefined;
};
