let scriptImports = [];
export const getScriptImports = () => scriptImports;
export const resetScriptImports = () => {
  scriptImports = [];
};

export const collectImportPlugin = () => {
  return {
    visitor: {
      ImportDeclaration(path) {
        scriptImports.push(path.node.source.value);
      },
    },
  };
};
