import { FC, memo, useEffect, useRef, useState } from "react";
import Box from "../Box";
import CodeEditor from "../CodeEditor";
import Resizer from "../Resizer";
import style from "./style.module.scss";
import { CodeBoxsProps } from "./types";
import { htmlOptions, scriptOptions, styleOptions } from "../dict";

const CodeBoxs: FC<CodeBoxsProps> = (props) => {
  const codeDivRef = useRef<HTMLDivElement>();
  const [loaded, setLoaded] = useState(false);
  const [codeDivRect, setCodeDivRect] = useState({ width: 0, height: 0 });
  //   const [widthList, setWidthList] = useState([0.3333, 0.3333, 0.3334]);

  const windowResize = (e) => {
    codeDivRef.current &&
      setCodeDivRect(codeDivRef.current.getBoundingClientRect());
  };

  const handleCodeTypeChange = (codeType: string, value: string) => {
    props.onChange(codeType, value);
  };

  useEffect(() => {
    document.addEventListener("resize", windowResize);

    setLoaded(true);
    return () => {
      document.removeEventListener("resize", windowResize);
    };
  }, []);

  const htmlType = props.value.get("htmlType") || "html" as any
  const cssType = props.value.get("cssType") || "css" as any
  const scriptType = props.value.get("scriptType") || "javascript" as any

  return (
    <div className={style["code-div"]} ref={codeDivRef}>
      {loaded ? (
        <>
          <Resizer>
            <Resizer.Item>
              <Box
                codeType={htmlType}
                options={htmlOptions}
                onChange={(value: string) =>
                  handleCodeTypeChange("htmlType", value)
                }
              >
                <CodeEditor
                  lang={htmlType}
                  value={props.value.get("html")}
                  onChange={(value) => {
                    props.onChange("html", value);
                  }}
                />
              </Box>
            </Resizer.Item>
            <Resizer.Item>
              <Box
                codeType={cssType}
                options={styleOptions}
                onChange={(value: string) =>
                  handleCodeTypeChange("cssType", value)
                }
              >
                <CodeEditor
                  lang={cssType}
                  value={props.value.get("css")}
                  onChange={(value) => {
                    props.onChange("css", value);
                  }}
                />
              </Box>
            </Resizer.Item>
            <Resizer.Item>
              <Box
                codeType={scriptType}
                options={scriptOptions}
                onChange={(value: string) =>
                  handleCodeTypeChange("scriptType", value)
                }
              >
                <CodeEditor
                  lang={scriptType}
                  value={props.value.get("script")}
                  onChange={(value) => {
                    props.onChange("script", value);
                  }}
                />
              </Box>
            </Resizer.Item>
          </Resizer>
        </>
      ) : null}
    </div>
  );
};

export default memo(CodeBoxs);
