export const formatLang = (lang: string): string => {
  switch (lang) {
    case "typescript":
      return "javascript";
    case "sass":
      return "css";
    case "react-js":
      return "javascript";
    case "react-ts":
      return "javascript";
    default:
      return lang;
  }
};

export const formatLangConfig = (lang: string): Record<any, any> => {
  switch (lang) {
    case "typescript":
      return {
        "typescript⁠": true,
      };
    case "react-js":
      return {
        jsx: true,
      };
    case "react-ts":
      return {
        jsx: true,
        "typescript⁠": true,
      };
    default:
      return {};
  }
};
