export const mergeImports = (
  ...rest: Record<string, string>[]
): Record<string, string> => {
  const imports: Record<string, string> = {};
  rest.forEach((map) => {
    if (map) {
      Object.assign(imports, map);
    }
  });

  return imports;
};

export const importmapScript = (imports: Record<string, string>): string => {
  const Obj = {
    imports,
  };
  return `<script type="importmap">${JSON.stringify(Obj, undefined, 2)}</script>`;
};
