import * as Babel from "@babel/standalone";
import { ParserReturn } from "../type";
import { TransformOptions } from "@babel/core";
import {
  collectImportPlugin,
  getScriptImports,
  resetScriptImports,
} from "./collectImportPlugin";

Babel.registerPlugin("collectImportPlugin", collectImportPlugin);

const getOptions = (scriptType: string) => {
  const config: TransformOptions = {
    presets: [],
    plugins: ["collectImportPlugin"],
    // ast: true,
  };
  switch (scriptType) {
    case "typescript":
      config.plugins.push(["transform-typescript"]);
      break;
    case "react-js":
      config.presets.push("react");
      break;
    case "react-ts":
      config.presets.push("react");
      config.plugins.push([
        "transform-typescript",
        {
          isTSX: true,
        },
      ]);
      break;
  }
  return config;
};

const importNeedESMPrefix = (value: string): boolean => {
  //判断import是否是以: http(s) | ./ | ../ | / 开头的，就不自动拼接esm
  if (/^[http|\.+/|\/]/.test(value)) {
    return false;
  }
  
  return true;
}

// 添加依赖
const addDependencies = (scriptType: string): Record<string, string> => {
  const imports: Record<string, string> = {};
  // switch (scriptType) {
  //   case "react-js":
  //   case "react-ts":
  //     imports.react = "https://esm.sh/react";
  //     imports["react-dom"] = "https://esm.sh/react-dom";
  //     break;
  // }

  // 通过import自动拼接https://esm.sh/
  const scriptImports = getScriptImports();
  scriptImports.forEach((item) => {
    if (importNeedESMPrefix(item)) {
      imports[item] = `https://esm.sh/${item}`;
    }
  });

  return imports;
};

const babelTransform = (scriptType: string, input: string) => {
  try {
    const outputCode = Babel.transform(input, getOptions(scriptType)).code;
    return `<script type="module">${outputCode}</script>`;
  } catch (err) {
    return "";
  }
};

export const transformScript = (
  scriptType: string,
  input: string
): ParserReturn => {
  resetScriptImports();

  const code = babelTransform(scriptType, input);

  try {
    return {
      code,
      imports: addDependencies(scriptType),
    };
  } catch (err) {
    console.error(err);
    return { imports: {}, code: "" };
  }
};
