import React, { memo, useEffect, useState } from "react";
import CodeBoxs from "./components/CodeBoxs";
import Previewer from "../components/Previewer";
import style from "./style.module.scss";
import Resizer from "./components/Resizer";

const keydown = function (e) {
  if (
    e.keyCode == 83 &&
    (navigator.platform.match("Mac") ? e.metaKey : e.ctrlKey)
  ) {
    e.preventDefault();
    // 阻止浏览器的默认保存行为
  }
};

const dataKeys = ["html", "htmlType", "css", "cssType", "script", "scriptType"];

const Editor = (props) => {
  const useCacheData = typeof window !== "undefined" && props.cachaValue;
  const initValue = dataKeys.map((key) => [
    key,
    useCacheData
      ? localStorage?.getItem?.(`${location.pathname}_${key}`)
      : props.data?.[key],
  ]);

  const [codeValue, setCodeValue] = useState(new Map(initValue));

  const onCodeChange = (key, value) => {
    codeValue.set(key, value);
    setCodeValue(new Map(codeValue));
    if (useCacheData) {
      localStorage?.setItem?.(`${location.pathname}_${key}`, value);
    }
  };

  useEffect(() => {
    document.addEventListener("keydown", keydown);

    return () => {
      document.removeEventListener("keydown", keydown);
    };
  }, []);

  return (
    <div className={style["editor"]}>
      <div className={style["iframe-div"]}>
        <Resizer type="vertical">
          <Resizer.Item>
            <CodeBoxs value={codeValue} onChange={onCodeChange} />
          </Resizer.Item>
          <Resizer.Item>
            <Previewer
              className={style["preview-iframe"]}
              codeMap={codeValue}
            />
          </Resizer.Item>
        </Resizer>
      </div>
    </div>
  );
};

export default memo(Editor);
