import React, { FC, memo, useEffect, useMemo, useRef, useState } from "react";
import { ResizerProps } from "./types";
import { ResizeCallbackData } from "react-resizable";
import { useDebounceFn, useMount } from "let-hooks/lib";
import classNames from "classnames";
import "react-resizable/css/styles.css";
import style from "./style.module.scss";

const Resizer: FC<ResizerProps> = (props): JSX.Element => {
  const items = React.Children.toArray(props.children);
  const avgValue = 1 / items.length;
  const isVertical = props.type === "vertical";

  const _ref = useRef<HTMLDivElement>(null);

  const [load, setLoad] = useState(false);
  const [resizing, setEesizing] = useState(false);

  // value 根据type来确定，默认为宽度,type=vertical时为高度
  const [valueMap, setValueMap] = useState<{ width: number; height: number }[]>(
    []
  );

  const getParentValue = () =>
    isVertical ? _ref.current.clientHeight : _ref.current.clientWidth;

  // 计算初始宽度
  const calcWidth = () => {
    const parentValue = getParentValue();

    items.map((_, index) => {
      const value =
        index !== items.length - 1
          ? avgValue * parentValue
          : (1 - avgValue * (items.length - 1)) * parentValue;
      valueMap[index] = {
        width: isVertical ? _ref.current.clientWidth : value,
        height: isVertical ? value : _ref.current.clientHeight,
      };
    });
    setValueMap([...valueMap]);
  };

  const onResize = useDebounceFn(() => {
    // 窗口改变的时候修改最后一个元素的高度和宽度
    const newValueMap = valueMap.map(item => {
      if (isVertical) {
        item.width = _ref.current.clientWidth
      } else {
        item.height = _ref.current.clientHeight
      }

      return item;
    })

    setValueMap([...newValueMap])
  }, 100)

  useMount(() => {
    setLoad(true);
    calcWidth();
  });


  useEffect(() => {
    let observer = new ResizeObserver(onResize.run);
    observer.observe(_ref.current);
    return () => {
      _ref.current && observer.unobserve(_ref.current);
    };
  }, []);

  const handleResize =
    (index: number) =>
      (e: React.SyntheticEvent<Element>, data: ResizeCallbackData) => {
        const attr = isVertical ? "height" : "width";
        // 确定index所对应的元素大小应该增大还是减少，减少就压缩左右的元素，增大就压缩右边的元素
        const isGT = data.size[attr] - valueMap[index][attr];
        valueMap[index] = data.size;
        // 确定应该减少宽度的index
        let needIncIndex = 0;
        if (items.length > 1) {
          if (isGT) {
            needIncIndex = index + 1;
          } else {
            // 减少左边的宽度
            if (index - 1 >= 0) {
              needIncIndex = index - 1;
            } else if (index + 1 <= items.length - 1) {
              needIncIndex = index + 1;
            }
          }

          // 计算needIncIndex元素的大小
          const usedValue = valueMap.reduce((prev, cur, index) => {
            if (needIncIndex !== index) {
              return prev + cur[attr];
            }
            return prev;
          }, 0);
          const parentValue = getParentValue();
          valueMap[needIncIndex] = {
            ...valueMap[needIncIndex],
            [attr]: parentValue - usedValue,
          };
        }

        setValueMap([...valueMap]);
      };

  const onResizeStart = () => {
    setEesizing(true);
  };

  const onResizeStop = () => {
    setEesizing(false);
  };

  const realItems = useMemo(() => {
    return items.map((ele: React.ReactElement, index) => {
      const value = valueMap[index];
      return React.cloneElement(ele, {
        index,
        value: value,
        onResize: handleResize(index),
        isFirst: index === 0,
        isLast: index === items.length - 1,
        type: props.type,
        resizing,
        onResizeStart,
        onResizeStop,
      });
    });
  }, [items, valueMap, load, resizing]);

  return (
    <div
      className={classNames(style["vwood-resizer"], {
        [style["vwood-resizer-vertical"]]: isVertical,
      })}
      ref={_ref}
    >
      {load ? realItems : null}
    </div>
  );
};

export default memo(Resizer);
