import { CSSProperties, useMemo } from "react";
import React from "react";
import { Resizable } from "react-resizable";
import { ResizerItemProps } from "./types";
import styles from "./style.module.scss";

const ResizerItem = (
  props: ResizerItemProps
) => {
  const { onResize, value, children, isLast } = props;

  const isVertical = props.type === "vertical";

  const style = useMemo((): CSSProperties => {
    return {
      width: `${value.width}px`,
      height: `${value.height}px`,
      pointerEvents: props.resizing ? "none" : undefined,
      userSelect: props.resizing ? "none" : undefined,
    };
  }, [value.height, value.width, isVertical, props.resizing]);

  const render = () => {
    return (
      <div className={styles["editor-resizer-item"]} style={style}>
        <div
          style={{
            flex: 1,
            display: "flex",
            overflow: "hidden",
          }}
          className={styles["editor-resizer-item-content"]}
        >
          {children}
        </div>
      </div>
    );
  };

  if (isLast) {
    return render();
  }

  return (
    <Resizable
      width={value.width}
      height={value.height}
      axis={isVertical ? "y" : "x"}
      onResizeStart={props.onResizeStart}
      onResizeStop={props.onResizeStop}
      handle={
        isLast ? null : (
          <div
            className={
              isVertical
                ? "react-resizable-handle-vertical"
                : "react-resizable-handle"
            }
            onClick={(e) => {
              e.stopPropagation();
            }}
          />
        )
      }
      onResize={onResize}
      draggableOpts={{ enableUserSelectHack: true, allowAnyClick: true }}
    >
      {render()}
    </Resizable>
  );
};

export default ResizerItem;
