import React, { FC, memo } from "react";
import { BoxProps } from "./types";
import style from "./style.module.scss";
import classNames from "classnames";
import { Select } from "antd";

const Box: FC<BoxProps> = (props): JSX.Element => {
  const { extra, children } = props;

  return (
    <div className={classNames(style["box"], props.className)} style={props.style ?? {}}>
      <div className={style["box-header"]}>
        <div className={style["box-header-left"]}>
          <Select size="small" options={props.options} value={props.codeType} onChange={props.onChange}></Select>
        </div>
        <div className={style["box-header-right"]}>{extra}</div>
      </div>
      <div className={style["box-body"]}>{children}</div>
    </div>
  );
};

export default memo(Box);
