import React, {
  FC,
  memo,
  useEffect,
  useMemo,
  useState,
} from "react";
import { PreviewProps } from "./types";
import classNames from "classnames";
import { codeParser } from "./parser";
import { useDebounceFn } from "let-hooks/lib";
import { CodeParserReturn } from "./parser/type";
import style from "./style.module.scss";

const Preview: FC<PreviewProps> = (props): JSX.Element => {
  const codeMap = props.codeMap ?? new Map();
  const [codeParserResult, setCodeParserResult] = useState<CodeParserReturn>();

  const parseObj = useDebounceFn(async() => {
   try {
    const result = await codeParser(codeMap)
    setCodeParserResult(result)
   } catch (err) {
   }
  }, 1000)

  const srcDoc = useMemo(() => {
    return `
      <html lang="en">
      <head>
          <meta charset="UTF-8">
          <meta name="apple-mobile-web-app-title" content="code preview">
          <title>code preview</title>
          ${codeParserResult?.importMap ?? ""}
          ${codeParserResult?.cssCode ?? ""}
      </head>
      <body>
      ${codeParserResult?.htmlCode ?? ""}
      ${codeParserResult?.scriptCode ?? ""}
      </body>
      </html>
    `;
  }, [codeParserResult]);

  useEffect(() => {
    parseObj.run();
  }, [codeMap])

  return (
    <iframe
      srcDoc={srcDoc}
      width="100%"
      className={classNames(style["iframe"], props.className)}
      title="code preview"
      sandbox="allow-forms allow-modals allow-pointer-lock allow-popups allow-same-origin allow-scripts allow-top-navigation-by-user-activation allow-downloads allow-presentation"
      allow="accelerometer; camera; encrypted-media; display-capture; geolocation; gyroscope; microphone; midi; clipboard-read; clipboard-write; web-share"
      scrolling="auto"
      allowTransparency
      //   allowpaymentrequest="true"
      allowFullScreen
      loading="lazy"
      frameBorder="none"
    />
  );
};

export default memo(Preview);
