import React, { FC, memo, useCallback, useEffect, useRef } from "react";
import { CodeEditorProps } from "./types";
import { EditorView, basicSetup } from "codemirror";
import { Compartment, EditorState } from "@codemirror/state";
import { oneDark } from "@codemirror/theme-one-dark";
import {
  drawSelection,
  keymap,
  lineNumbers,
  ViewUpdate,
} from "@codemirror/view";
import { defaultKeymap, historyKeymap, history } from "@codemirror/commands";
import { getEditorLangModule } from "./langImport";
import style from "./style.module.scss";
import { formatLang, formatLangConfig } from "./langFormat";

const editorTheme = new Compartment();
const darkTheme = editorTheme.of(oneDark);
// react-codemirror 地址
// https://github.com/uiwjs/react-codemirror/blob/master/core/src/useCodeMirror.ts

const CodeEditor: FC<CodeEditorProps> = (props): JSX.Element => {
  const codeRef = useRef<HTMLDivElement>();
  const _editorRef = useRef<EditorView>();

  const handleChange = useCallback((vu: ViewUpdate) => {
    if (vu.docChanged && typeof props.onChange === "function") {
      const doc = vu.state.doc;
      const value = doc.toString();
      props.onChange(value);
    }
  }, []);

  const initEditor = useCallback(async () => {
    const lang = formatLang(props.lang) || "html"
    const langModule = await getEditorLangModule(lang);
    if (!langModule) {
      return;
    }
    const extensions = [
      basicSetup,
      langModule[0]?.(formatLangConfig(props.lang)),
      langModule[1],
      darkTheme,
      history(),
      drawSelection(),
      lineNumbers(),
      keymap.of([
        ...defaultKeymap,
        ...historyKeymap,
        {
          key: "Mod-s",
          mac: "Mod-s",
          run: () => {
            // 出发保存行为
            props.onChange?.(_editorRef.current.state.doc.toString());
          },
        },
      ] as any),
      EditorView.updateListener.of(handleChange),
    ];
    if (props.readonly) {
      extensions.push(EditorState.readOnly.of(true));
    }

    _editorRef.current = new EditorView({
      doc: props.value,
      extensions: extensions,
      parent: codeRef.current,
    });
  }, [props.lang, props.config]);

  useEffect(() => {
    initEditor();

    return () => {
      if (_editorRef.current) {
        _editorRef.current.destroy();
      }
    };
  }, [props.lang, initEditor]);

  return <div ref={codeRef} className={style["code-editor"]}></div>;
};

export default memo(CodeEditor);
