import { CSSType } from "../../types";
import { ParserReturn } from "../type";
import { cssParser } from "./cssParser";
import { sassParser } from "./sassParser";
import postcss from "postcss";
import autoprefixer from "autoprefixer";
const prefixer = postcss([autoprefixer]);

export const parserMap: Partial<
  Record<CSSType, (code: string) => ParserReturn>
> = {
  [CSSType.SASS]: sassParser,
  [CSSType.CSS]: cssParser,
};

export const styleParser = async (
  cssType: CSSType = CSSType.CSS,
  css: string
): Promise<ParserReturn> => {
  const praserResult = parserMap[cssType]?.(css);
  const postcssResult = await prefixer.process(praserResult.code);

  return {
    ...praserResult,
    code: `<link href="https://esm.sh/normalize.css" rel="stylesheet" type="text/css"><style>${postcssResult.css}</style>`,
  };
};
