import { transformScript } from "./scriptParser/babelTransform";
import { styleParser } from "./styleParser";
import { CodeParserReturn } from "./type";
import { importmapScript, mergeImports } from "./utils";

export const codeParser = async (codeMap: Map<string, any>): Promise<CodeParserReturn> => {
  const cssParserResult = await styleParser(
    codeMap.get("cssType"),
    codeMap.get("css")
  );
  const scriptParserResult = transformScript(
    codeMap.get("scriptType"),
    codeMap.get("script") ?? ""
  );
  return {
    importMap: importmapScript(
      mergeImports(cssParserResult.imports, scriptParserResult.imports)
    ),
    htmlCode: codeMap.get("html") ?? "",
    cssCode: cssParserResult.code,
    scriptCode: scriptParserResult.code,
  };
};
